import type { FC, ReactNode } from "react";
import { twMerge } from "tailwind-merge";

interface ITextProps {
  children: ReactNode;
  className?: string;
}

const Text: FC<ITextProps> = ({ children, className }) => {
  return (
    <p className={twMerge("text-base text-gray-500", className)}>{children}</p>
  );
};

export default Text;
