import type { FC, ReactNode } from "react";
import { twMerge } from "tailwind-merge";

interface IH1Props {
  children: ReactNode;
  className?: string;
}

const H1: FC<IH1Props> = ({ children, className }) => {
  return (
    <h1 className={twMerge("text-6xl font-bold text-slate-700", className)}>
      {children}
    </h1>
  );
};

export default H1;
