import type { FC, ReactNode } from "react";
import { twMerge } from "tailwind-merge";

interface ICodeProps {
  children: ReactNode;
  className?: string;
}

const Code: FC<ICodeProps> = ({ children, className }) => {
  return (
    <code
      className={twMerge(
        "text-xl p-4 bg-orange-100 rounded text-yellow-800",
        className,
      )}
    >
      {children}
    </code>
  );
};

export default Code;
