import type { FC, ReactNode } from "react";
import { twMerge } from "tailwind-merge";

interface IH4Props {
  children: ReactNode;
  className?: string;
}

const H4: FC<IH4Props> = ({ children, className }) => {
  return (
    <h4 className={twMerge("text-lg font-semibold text-slate-700", className)}>
      {children}
    </h4>
  );
};

export default H4;
