import type { FC, ReactNode } from "react";
import { twMerge } from "tailwind-merge";

interface IH2Props {
  children: ReactNode;
  className?: string;
}

const H2: FC<IH2Props> = ({ children, className }) => {
  return (
    <h2 className={twMerge("text-5xl font-bold text-slate-700", className)}>
      {children}
    </h2>
  );
};

export default H2;
