import type { FC, ReactNode } from "react";
import { twMerge } from "tailwind-merge";

interface IH3Props {
  children: ReactNode;
  className?: string;
}

const H3: FC<IH3Props> = ({ children, className }) => {
  return (
    <h3 className={twMerge("text-3xl text-slate-700", className)}>
      {children}
    </h3>
  );
};

export default H3;
